.items-list li:first-child{
    justify-content: center;
}

.items-list .apps{
    cursor: pointer;
}

.items-list .apps h3{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.items-list .logout:hover,
.items-list .apps:hover{
    background-color: #414141;
}

.items-list .no-apps p{
    text-align: center;
    width: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
}