.alert-container{
    width: 100%; 
    position: fixed;
    margin-top: 10px !important;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    z-index: 10;
}

@media (min-width: 500px) {
    .alert-container{
        width: 80%; 
    }
}

@media (min-width: 750px) {
    .alert-container{
        width: 60%; 
    }
}

@media (min-width: 950px) {
    .alert-container{
        width: 40%; 
    }
}