.header-container{
    max-width: 100vw;
    overflow: hidden;
}

.nav-bar{
    width: 100vw;
    background-color: var(--primary-color);
}

.nav-bar .nav-conatiner{
    margin: auto;
    padding: 0 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    max-width: 2200px;
}



.nav-bar .nav-conatiner > div {
    /* display: inline-block; */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* padding: 8px; */
}

.nav-bar .nav-conatiner .logout{
    display: none;
}

.nav-bar .nav-conatiner > div > div:not(.current-app){
    padding: 10px;
    display: grid;
}

.menu,
.appDrawer,
.logout{
    cursor: pointer;
}

.menu:hover svg,
.appDrawer:hover svg,
.logout:hover{
    /* font-size: 1.6rem; */
    scale: 1.1;
}

.nav-bar .nav-conatiner .logo img{
    width: 40px;
    padding-right: 15px;
    border-right: 2px solid #737272;
    stroke-dasharray: 44;
    stroke-dashoffset: 44;
}
.nav-bar .nav-conatiner .logo img:nth-child(1){
    display: none;
} 

@media ( min-width: 750px ) {
    .nav-bar .nav-conatiner .logo img{
        width: auto;
    }

    .nav-bar .nav-conatiner > div > div:not(.current-app){
        padding: 15px;
    }

    .nav-bar .nav-conatiner .logout{
        display: grid;
    }
}

@media ( min-width: 500px) {
    .nav-bar .nav-conatiner .logo img:nth-child(1){
        display: block;
        width: auto;
    }  

    .nav-bar .nav-conatiner .logo img:nth-child(2){
        display: none;
    }
}
